import Header from '~/components/Header'
import VideoBanner from '~/components/VideoBanner'
import About from '~/components/About'
import Facilities from '~/components/Facilities'
import Expertise from '~/components/Expertise'
import Testimonials from '~/components/Testimonials'
import AskDoctors from '~/components/AskDoctors'
import CallAction from '~/components/CallAction'
import Clinics from '~/components/Clinics'
import Footer from '~/components/Footer'
import Hidden from '~/components/Hidden'
import PageNav from '~/components/PageNav'
import Notification from '~/components/Notification'
import DotsParallax from '~/components/Parallax/Dots'
import OvalTop from '~/components/Parallax/OvalTop'
import brand from '~/static/text/brand'

export default {
  components: {
    'main-header': Header,
    DotsParallax,
    OvalTop,
    VideoBanner,
    About,
    Facilities,
    Expertise,
    Testimonials,
    AskDoctors,
    CallAction,
    Clinics,
    PageNav,
    Hidden,
    Notification,
    'main-footer': Footer
  },
  computed: {
    isTablet() {
      return (
        this.$mq === 'mdDown' || this.$mq === 'smDown' || this.$mq === 'xsDown'
      ) // eslint-disable-line
    },
    isMobile() {
      return this.$mq === 'smDown' || this.$mq === 'xsDown'
    }
  },
  head() {
    return {
      title: brand.medical.name + ' - Home Page'
    }
  }
}
